// components/dashboard/ComponentLoader.tsx
import { Suspense } from 'react';
import { Loader2 } from 'lucide-react';
import * as DashboardComponents from '@/components/dashboard';

interface ComponentLoaderProps {
  activeTab: string;
  activeSubItem: string | null;
  data?: any; // Replace with proper type when data structure is defined
}

const LoadingFallback = () => (
  <div className="w-full h-64 flex items-center justify-center">
    <Loader2 className="h-8 w-8 animate-spin text-indigo-600" />
  </div>
);

export const ComponentLoader: React.FC<ComponentLoaderProps> = ({ 
  activeTab, 
  activeSubItem,
  data 
}) => {
  const renderComponent = () => {
    switch (activeTab) {
      case 'overview':
        return <DashboardComponents.Overview stats={data} />;
      
      case 'traffic':
        switch (activeSubItem) {
          case 'website-traffic':
            return <DashboardComponents.WebsiteTraffic data={data} timeRange="7d" />;
          case 'social-traffic':
            return <DashboardComponents.SocialMediaTraffic data={data} timeRange="7d" />;
          case 'ads-performance':
            return <DashboardComponents.AdsPerformance data={data} timeRange="7d" />;
          default:
            return <DashboardComponents.WebsiteTraffic data={data} timeRange="7d" />;
        }
      
      case 'analytics':
        switch (activeSubItem) {
          case 'seo-performance':
            return <DashboardComponents.SEOPerformance data={data} timeRange="7d" />;
          case 'user-behavior':
            return <DashboardComponents.UserBehavior data={data} timeRange="7d" />;
          case 'historical-data':
            return <DashboardComponents.HistoricalData data={data} timeRange="7d" />;
          default:
            return <DashboardComponents.SEOPerformance data={data} timeRange="7d" />;
        }
      
      case 'reports':
        switch (activeSubItem) {
          case 'seo-reports':
            return <DashboardComponents.SEOReports reports={data} />;
          case 'traffic-reports':
            return <DashboardComponents.TrafficReports reports={data} />;
          case 'competitor-reports':
            return <DashboardComponents.CompetitorReports reports={data} />;
          case 'ads-reports':
            return <DashboardComponents.AdsReports reports={data} />;
          default:
            return <DashboardComponents.SEOReports reports={data} />;
        }
      
      case 'files':
        switch (activeSubItem) {
          case 'scraped-webpages':
            return <DashboardComponents.ScrapedWebpages files={data} />;
          case 'reports-documents':
            return <DashboardComponents.ReportsDocuments files={data} />;
          case 'competitor-data':
            return <DashboardComponents.CompetitorData files={data} />;
          default:
            return <DashboardComponents.ScrapedWebpages files={data} />;
        }
      
      case 'ads':
        switch (activeSubItem) {
          case 'my-ads':
            return <DashboardComponents.MyAds ads={data} />;
          case 'competitor-ads':
            return <DashboardComponents.CompetitorAds ads={data} />;
          default:
            return <DashboardComponents.MyAds ads={data} />;
        }
      
      case 'competitor':
        return activeSubItem === 'details' 
          ? <DashboardComponents.CompetitorDetails competitor={data} />
          : <DashboardComponents.CompetitorGrid competitors={data} />;
      
      case 'ai':
        switch (activeSubItem) {
          case 'ai-fixes':
            return <DashboardComponents.AIFixes data={data} />;
          case 'alerts-monitoring':
            return <DashboardComponents.AlertsMonitoring data={data} />;
          case 'ai-content':
            return <DashboardComponents.AIContentGeneration data={data} />;
          default:
            return <DashboardComponents.AIFixes data={data} />;
        }
      
      case 'chat':
        return <DashboardComponents.ChatInterface messages={data} />;
      
      default:
        return <div>Component not found</div>;
    }
  };

  return (
    <Suspense fallback={<LoadingFallback />}>
      {renderComponent()}
    </Suspense>
  );
};